import React, {Component} from 'react';
import {Card, CardBody, CardHeader, Col, Row, Button} from 'reactstrap';
import feathers from '../../feathers';

import BootstrapTable from 'react-bootstrap-table-next';

class Subsidiary extends Component {
  constructor (props) {
    super (props);
    this.state = {
      data: [],
      total: '',
    };
  }

  async componentWillMount () {
    var _this = this;

    let subsidiaryResult = await feathers.service ('subsidiary').find ({
      query: {
        $populate: 'empresa',
      },
    });
    console.log ('subsidiaryResult', subsidiaryResult);

    this.setState ({
      data: subsidiaryResult.data,
    });
  }

  render () {
    var indexNumber = 0;

    const columns = [
      {
        dataField: 'empresa.nombre',
        text: 'Cliente',
        sort: true,
        formatter: (cell, row) => {
          return cell;
        },
      },
      {
        dataField: 'nombre',
        text: 'Nombre',
        sort: true,
        formatter: (cell, row) => {
          return cell;
        },
      },

      {
        dataField: 'telefono',
        text: 'Teléfono',
        sort: true,
        formatter: (cell, row) => {
          return cell;
        },
      },
      {
        dataField: 'cargaConectada',
        text: 'Carga conectada',
        sort: true,
        formatter: (cell, row) => {
          return cell;
        },
      },
      {
        dataField: 'demandaContratada',
        text: 'Demanda Contratada',
        sort: true,
        formatter: (cell, row) => {
          return cell;
        },
      },
      {
        dataField: 'nivelTension',
        text: 'Nivel de tensión',
        sort: true,
        formatter: (cell, row) => {
          return cell;
        },
      },

      {
        dataField: 'actions',
        text: 'Acciones',
        headerStyle: (colum, colIndex) => {
          return {width: 150};
        },
        formatter: (cell, row) => {
          return (
            <Row>
              <Col col="6" sm="4" md="2" xl className="mb-3 mb-xl-0">
                <Button
                  color="danger"
                  className="btn-square pull-right"
                  style={{marginLeft: 5}}
                >
                  <i className="fa fa-trash" />
                </Button>
                <Button
                  color="success"
                  className="btn-square pull-right"
                  onClick={() =>
                    this.props.history.push ('/centros-carga/' + row._id)}
                >
                  <i className="fa fa-pencil" /> Editar
                </Button>
              </Col>
            </Row>
          );
        },
      },
    ];

    return (
      <div className="animated fadeIn">
        <Row>
          <Col xs="12" lg="12">
            <Card>
              <CardHeader>
                Administración - Centros de Carga
                <div className="card-header-actions">
                  <i className="fa fa-download" />
                  <a href="http://ipco.com" className="card-header-action">
                    <small className="text-muted">EXPORTAR</small>
                  </a>
                </div>
              </CardHeader>
              <Row
                className="pull-right"
                style={{
                  marginTop: '5px',
                  paddingRight: '5px',
                }}
              >
                <Col col="12" sm="12" md="12">
                  <Button
                    onClick={() =>
                      this.props.history.push ('/centros-carga/form')}
                    color="success"
                    className="btn-square pull-right"
                  >
                    <i className="fa fa-plus" />{' '}
                    Agregar Centro de Carga
                  </Button>
                </Col>
              </Row>
              <Row>
                <CardBody style={{paddingTop: 8}}>
                  <BootstrapTable
                    keyField="_id"
                    data={this.state.data}
                    columns={columns}
                    bootstrap4
                    striped
                    hover
                  />
                </CardBody>
              </Row>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}

export default Subsidiary;
